/*Projects Container Section*/
.projectsSectionCont {
  width: 80%;
  margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-top: 12vh;

  }
  .projectsSectionCont>div:first-child
  {
    width: 4%;
    margin-bottom: 80px;
    border-top: 3px solid  #fdd807;
  }
  .projectsSectionCont  > p {
    font-weight: 500;
    font-size: 30px;
    color: rgb(202, 213, 219);;
  } 
  .projectsSectionCont  > p >span{
    font-weight: 500;
    color: #fdd807;
  }
  .stackIcon {
    color: rgb(8, 111, 143);
    font-size: 30px;
    margin: 5px;
  }
  a {
    text-decoration: none;
  }
  .projectGrid {
    display: grid;
    grid-template-columns: 48% 48%;
    gap:  6%;
    justify-content: center;
    padding-bottom: 30px;
  }
  /*project card*/
  .projectCont {
    width: 100%;
    margin: auto;
    height: 460px;
    border-radius: 5px;
    text-align: center;
    box-shadow: rgba(220, 245, 30, 0.15) 0px 2px 5px 0px,
      rgba(223, 212, 212, 0.05) 0px 1px 1px 0px;
      overflow: hidden;
  }
  .projectCont h2 {
    /* color: rgb(8, 111, 143); */
    text-align: center;
  }
  .image {
    border-radius: 5px;
    width: 100%;
    height: 55%;
    margin: auto;
    cursor: pointer;
    object-fit: cover;
  }
  .image:hover {
      transform: scale(1.1);
  }
  .info {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    padding: 5px 0;
  }
  .linkCont {
    display: flex;
    align-items: center;
    gap: 50%;
    justify-content: space-around;
  
    padding: 10px 0;
  }
  .linkCont > p {
    font-size: 25px;
    font-weight: 400;
    margin: 0;
  }
  .linkDiv {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    background-color: #fdd807;
    color: white;
    font-weight: 500;
    height: 40px;
    border-radius: 5px;
    width: 100px;
  }
  .linkDiv:hover {
    background-color:rgb(202, 213, 219);
    color: black;
  }
  .linkDiv p {
    margin: 5px;
  }
  .stacksDiv {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .stacksDiv2 {
    display: none;
  }
  .projInfo {
    width: 90%;
    margin: auto;
    text-align: left;
    overflow: hidden;
  }
  .projInfo > p {
    font-size: 17px;
    color:rgb(202, 213, 219);

  }
  @media only screen and (max-width: 1024px) {
    .projectsSectionCont {
      padding-bottom: 0;
    }
    .projectCont {
      width: 90%;
      /* height: 150px; */
      margin: 30px auto;
      height: auto;
    }
    .projectGrid {
      display: flex;
      flex-direction: column;
      gap: 5%;
    }
    .projInfo {
      display: none;
    }
    .stacksDiv {
      display: none;
    }
    .linkCont > p {
      display: none;
    }
    .stacksDiv2 {
      display: flex;
      justify-content: center;
      align-items: center;
      /* width: 50%; */
      margin: auto;
      margin-top: 10px;
    }
  }