
.stagecubecont
{
 width: 20%;
 top: 0;
 padding-top: 10%;
 padding-right: 10%;
 position: absolute;
 right: 0;
 /* overflow: hidden; */
}

.cubespinner
{
    animation-name: spincube;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
    animation-duration: 12s;
    transform-origin: 100px 100px 0 ;
    transform-style:preserve-3d ;
    margin-left: calc(50%-100px);
   
}

@keyframes spincube
{
    from,to{transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg)}
    16%{transform: rotateY(-90deg);};
    33%{transform: rotateY(-90deg) rotateZ(90deg);};
    50%{transform: rotateY(-180deg) rotateZ(90deg);}
    66%{transform: rotateY(-270deg) rotateZ(90deg);};
    83%{transform:  rotateX(-90deg);}
}

.cubespinner>div
{
  position: absolute;
  width: 185px;
  height: 185px;
  font-size: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0 10px 0px lightyellow;
}


.face1{
  transform: translateZ(100px);
}
.face2{
  transform: rotateY(90deg) translateZ(100px);
}
.face3{
  transform: rotateY(90deg) rotateX(90deg) translateZ(100px);
}
.face4{
  transform: rotateY(180deg) rotateZ(90deg) translateZ(100px);
}
.face5{
  transform: rotateY(-90deg) rotateZ(90deg) translateZ(100px);}
.face6{
  transform: rotateX(-90deg) translateZ(100px);
}



@media only screen and (max-width: 1024px) {
  
.stagecubecont
{
  width: 20%;
  margin: auto;
position: relative;
margin-bottom: 20%;
}
.cubespinner>div
{
  position: absolute;
  width: 180px;
  height: 180px;
  font-size: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0 10px 0px lightyellow;
}
.cubespinner
{
    animation-name: spincube;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
    animation-duration: 12s;
    transform-origin: 100px 100px 0 ;
    transform-style:preserve-3d ;
    margin-left: calc(50%-100px);
   
}

}