.rootCont {
    width:80%;
    margin: auto;
    text-align: center;
    flex-direction: column;
    padding-top: 20vh;
    /* border: 1px solid red; */
  }
  
  .nameCont {
    width: 60%;
    margin: auto;
    text-align: center;
    grid-area: nameCont;
    font-size: 42px;
    font-weight: 650;
    color: rgb(202, 213, 219);
    line-height: normal;
  }
  .btn
{
  background-color:#fdd807;
  padding: 8px 30px;
  cursor: pointer;
  font-size: 18px;
  font-weight: 550;
  margin-bottom: 100px;
  border-radius: 7px;
  margin-top: 30px;
}
.form
{
    width: 43%;
   background-color:#fdd807;
    border-radius: 7%;
    margin: auto;
    border: 1px solid ;
    margin-bottom: 30px;
}
  .nameCont * {
    margin: 0px 0;
  }
  .nameCont p span {
    color:#fdd807;
  }

  .contactHeading
  {
    font-size: x-large;
    font-weight: 500;
  }
.iconsCont {
    display: flex;
    margin: 5% 0px;
    align-items: center;
    justify-content: center;
    /* margin-bottom: 20px; */
  }
  .linkedInIcon {
    font-size: 40px;
    margin: 0 10px;
    cursor: pointer;
    color: white !important;
  }
  .linkedInIcon:hover {
    color: rgb(17, 121, 224) !important;
  }
  .gitIcon {
    font-size: 40px;
    margin: 0 10px;
    cursor: pointer;
    color: white !important;
  }
  .gitIcon:hover {
    color: rgb(80, 80, 80) !important;
  }
  .emailicon {
    font-size: 40px;
    margin: 0 10px;
    cursor: pointer;
    color: white !important;
  }
  .emailicon:hover {
    color: rgb(80, 80, 80) !important;
  }

 
  [type="submit"],[type="back"] {
    font-family: "Montserrat", Arial, Helvetica, sans-serif;
    width:30%;
    margin: 0px 40px;
    color:#fdd807 ;
    border-radius: 5px;
    border: 0;
    cursor: pointer;
    background-color:red;
    font-size: 16px; 
    padding: 10px 0px;
    transition: all 0.3s;
    margin-top: 30px;
    font-weight: 550;
  }
  [type="back"]
  {
    background-color: green;
  }
  [type="back"]:hover,[type="submit"]:hover {
    background-color:black;
    color: white;
  } 
  form {
    max-width: 420px;
    margin: 60px auto;
  }
  .form input
  {
    background-color: black;
  }
  .feedback_input {
    color: white;
    background-color: black;
    font-family: Helvetica, Arial, sans-serif;
    font-weight: 500;
    font-size: 16px;
    border-radius: 5px;
    line-height: 25px;
    background-color: transparent;
    border: 2px solid #dbd2d2;
    transition: all 0.3s;
    padding: 10px;
    margin-bottom: 15px;
    width: 90%;
    box-sizing: border-box;
    outline: 0;
  }
  .bottom>div
  {
    margin: 40px 0px;
    color: #fdd807 ;
  }


  
  @media only screen and (max-width: 1024px) {
    
  .nameCont {
    width: 80%;
    margin: auto;
    text-align: center;
    grid-area: nameCont;
    font-size: 30px;
    font-weight: 650;
    color: rgb(202, 213, 219);
    line-height: normal; 
  }
  }