.root {
    display: grid;
    grid-template-columns: 55% 45%;
    align-items: center;
    position: sticky;
    top: 0;
    width: 100%;
    background-color: #000;
    height: 50px;
    z-index: 1;
    font-size: 18px;
    font-weight: 500;
    border-bottom:1px solid ;
    color:#fdd807;
    padding: 10px 0px;
}

  body
  {
    background-color: #000;
  } 
  a {
    height: 50px;
  }
  .navsCont {
    display: grid;
    grid-template-columns: repeat(4, 24%);
    align-items: center;
  }
  .nav {
    cursor: pointer;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .nav:hover
  {
    transform: scale(1.2)
  }
 
  .navlogo {
    cursor: pointer;
    width: 70px;
    text-align: center;
    margin-left: 10%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 45px;
  }
  .logoimg {
    height:60px;
    border-radius: 50%;
    margin-top: 6px; 
    margin-left: 100px;
  }
  @media only screen and (max-width: 1024px) {
    .root {
      grid-template-columns: 30% 60%;
      font-size: 18px;
      justify-content: space-around;
    }
    button
    {
      font-size: xx-small;
    } 
    .navsCont {
      justify-content: space-around;
    }
    .nav {
      width: 100%;
    }
    .navlogo {
      width: 50px;
    }
    .nav:hover {
      transform: scale(1.2);
    }
    .navlogo:hover {
      border-bottom: none;
    }
    .active > .nav {
      border-bottom: none;
    }
    .active > .navlogo {
      border-bottom: none;
    }
  }

  @media only screen and (max-width: 884px) {
    .root {
      /* grid-template-columns: 30% 60%; */
      font-size: 15px;
      justify-content: space-around;
    }
    .navsCont {
      justify-content: space-evenly;
    }
    .nav {
      /* height: 47px; */
      width: 100%;
    }
    .navlogo {
      width: 35px;
    }
    .nav:hover{
      transform:scale(1.1);

    }
    .navlogo:hover {

      border-bottom: none;
    }
    .active > .nav {
      border-bottom: none;
    }
    .active > .navlogo {
      border-bottom: none;
    }
  }