.rootCont {
    display: grid;
    grid-template-areas:
      "nameCont photoCont"
      "socialCont .";
    grid-template-columns: 70% 40%;
    width: 80%;
    margin: auto;
    align-items: center;
    justify-content: center;
    padding-top: 30vh;
    padding-bottom: 25vh;
  }
  a {
    text-decoration: none;
  }
  .nameCont {
    grid-area: nameCont;
    width: 100%; 
    font-size: 45px;
    font-weight: 650;
    color: rgb(202, 213, 219);
    line-height: normal;
  }

  .nameCont * {
    margin: 0px 0;
  }
  .nameCont p span {
    color:#fdd807;
  }
  .photoCont {
    grid-area: photoCont;
    width: 100%;
    max-height: 60vh;
  }

 
  .contactBtn {
    color: white;
  }
  .heroImg {
    margin-left: 100px;
    margin-top: 20px;
    width: 70%;
    border-radius: 50%;
  }
  

  .cardTamp{
    align-items: center;
    align-content: center;
    justify-content: center;
    justify-items: center;
    border-radius: 35px;
    gap:30%;
    width: 380px;
    display: flex;
  }

.btn
{
  display: flex;
  gap:30%;
  margin: 7%;
  /* justify-content: space-around; */
}
.btn button:first-child
{
  background-color: rgb(202, 213, 219);
  padding: 15px 25px;
  border-radius: 7px;
  cursor: pointer;
  font-size: medium;
  font-weight: 600;
}

.btn>button:last-child
{
  background-color:#fdd807;
  padding: 12px 25px;
  border-radius: 7px;
  cursor: pointer;
  font-size: medium;
  font-weight: 600;
}

.btn button:last-child:hover
{
  /* width: 19.59%; */
  padding-right: 32px;
  text-align: left;
  background-color: rgb(202, 213, 219);
  border-radius: 7px;
}

.btn button:first-child:hover
{
  /* width: 19.59%; */
  text-align: left;
  background-color:#fdd807;
  padding-right:30px;
  border-radius: 7px;
}


@media only screen and (max-width: 1024px) {
  .rootCont {
    grid-template-areas:
      "nameCont nameCont "
      "socialCont photoCont";
    width: 90%;
    grid-template-columns: 20% 80%;
    align-items: flex-start;
    /* padding-top: 7vh; */
  }

  .photoCont {
    padding-top: 30px;
    max-height: 50vh;
  }
  .heroImg{
    margin-top: -5px;
    margin-left: 20px;
  }

.nameCont {
  grid-area: nameCont;
  font-size: xx-large;
  width: 500px;
}
.btn>button:last-child
{
  font-size: small;
  font-weight: 550;
}
.btn button:first-child
{  width: 125px;padding: 15px 20.2px;
  font-size: small;
  text-align: center;
  font-weight: 550;
}

}

