.rootCont {
    width:80%;
    margin: auto;
    text-align: center;
    flex-direction: column;
    padding: 13vh;
  }
  .rootCont>div:first-child
  {
    width: 4%;
    margin-bottom: 60px;
    border-top: 3px solid  #fdd807;
  }
  .rootCont h1 {
    width: 95px;
    margin: 0 auto;
  }
  .rootCont > p {
    font-size: 50px;
    font-weight: 400;
    margin-top: 100px;
  }
  .mainCont {
    display: grid;
    grid-template-columns: 50% 30%;
    font-size: 20px;
  }


  
  
.nameCont { 
  grid-area: nameCont;
  width: 100%;
  font-size: 30px;
  font-weight: 550;
  color: rgb(202, 213, 219);
  line-height: normal;
}
.para
{
  width:100%;
  font-size: 17px;
  text-align: justify;
  color: rgb(202, 213, 219);
  line-height: normal;
  font-weight: 340;
 
}

  .nameCont p span {
    color:#fdd807;
  }
  .mainCont > div {
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    justify-content: center;
    text-align: left !important;
  }
  .mainCont > div > h2 {
    margin: 0px;
  }
  
  
  
  .stat{
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    border: 1px solid gray.200;
    height: auto;
    width: auto;
    display: flex;
    gap:10px;
    padding: 20px;
  }
  
  .stat>div{
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    height: auto;
    width: 30%;
    padding: 3px;
    background-image: linear-gradient(to right, #cc2b5e  ,  #753a88);
    color: #fff;
  }
  .main :nth-child(1) {
    font-size : large;
    font-weight: bolder;
  }
  
  .main :nth-child(2) {
    font-size : large;
    font-weight: bolder;
  }
  
  
  @media only screen and (max-width: 1024px) {
    .rootCont {
      width: 90%;
      margin: auto;
    }
    
